import React, { Component, Fragment } from "react";
import { TweenMax, TimelineMax, EasePack, TextPlugin} from 'gsap';
import ReactGA from 'react-ga';
import Plx from 'react-plx';
import Page from "../layouts/main";
import Landing from "../sections/Landing/Landing";
import Work from "../sections/Work/Work";
import Contact from "../sections/Contact/Contact";
import WhoAmI from "../sections/WhoAmI/WhoAmI";
import settings from "../util/settings";
import data from '../static/json/data.json';

class Index extends Component {

  constructor(props) {
    super(props);
    this.state = {
      animDone: false,
      detect: {}
    }
  }

  componentDidMount() {
    window.addEventListener('resize', this.handleResize);
    this.setState({detect: this.Page.getDetect()})
  }

  componentWillUnmount() {
    window.removeEventListener('resize', this.handleResize);
  }

  handleResize = () => {
    if (!settings.isServer()) {
      var orientation = this.state.orientation;
      if (window.innerHeight >= window.innerWidth) {
        orientation = "portrait"
      } else {
        orientation = "landscape"
      }
      if (orientation != this.state.orientation) {
        document.body.className = document.body.className.replace(this.state.orientation, orientation);
        this.setState({orientation: orientation});
      }
    }
  }

  setAnimDone = () => {
    this.setState({animDone: true})
  }

  render() {

    return (
      <Page id="Home" showMenu={this.state.animDone} ref={el => this.Page = el}>

        <Landing scrollToSection={this.Page ? this.Page.scrollToSection : () => {}}
                 animDone={this.state.animDone}
                 setAnimDone={this.setAnimDone} />

        { this.state.animDone && (
            <Fragment>
              <Work data={data.projects} detect={this.Page && this.Page.getDetect()}/>
              <WhoAmI data={data.features} />
              <Contact />
            </Fragment>
          )
        }

      </Page>
    )
  }
}

export default Index



